html{
  background: royalblue;
  text-align: center;
  font-size: 15px;
}

body{
  font-size: 2rem;
  display: flex;
  align-items: center;
  flex: 1;
  min-height: 100vh;

}

.clock{
  width: 30rem;
  height: 30rem;
  border: 20px solid white;
  border-radius: 50%;
  margin: 50px auto;
  position: relative;
  padding: 2rem;
}

.hand{
  width: 50%;
  height: 6px;
  background: black;
  position: absolute;
  top:50%;
  transform-origin: 100%;
  transform: rotate(90deg);
  transition: all 0.05s ;
  transition-timing-function:cubic-bezier(0.1, 2.7, 0.58,1);


}


.clock-face{
  position: relative;
  width: 100%;
  height: 100%;
  transform: translateY(-3px);
}

